"use client";

import { useState, useEffect } from "react";
import { motion } from "framer-motion";
import { Tab, Tabs, TabList, TabPanel } from "react-tabs";
import "react-tabs/style/react-tabs.css";
import Link from "next/link";
import { Navbar } from "@material-tailwind/react";

const LandingPage = () => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [expandedFeature, setExpandedFeature] = useState(null);
  const [activeFeature, setActiveFeature] = useState(null);
  const [activeTab, setActiveTab] = useState("Why Choose Us?");

  useEffect(() => {
    const script1 = document.createElement("script");
    script1.src = "https://cdn.botpress.cloud/webchat/v2.2/inject.js";
    script1.async = true;
    document.body.appendChild(script1);

    const script2 = document.createElement("script");
    script2.src = "https://files.bpcontent.cloud/2025/02/25/14/20250225142213-81JJQBFZ.js";
    script2.async = true;
    document.body.appendChild(script2);

    script1.onload = () => {
      if (window.botpressWebChat) {
        window.botpressWebChat.init({
          botId: "eeb435e2-2559-43fa-af71-f7d4cff6e9b4",
          host: "https://cdn.botpress.cloud/webchat",
          messagingUrl: "https://messaging.botpress.cloud",
          showPoweredBy: false,
          enableTranscriptDownload: true,
          stylesheet: "https://cdn.botpress.cloud/webchat/v1/style.css",
        });
      }
    };
  }, []);

  const features = [
    {
      title: "AI-Driven Job Matching",
      shortDescription: "Get personalized job recommendations based on your skills.",
      fullDescription: "Our AI analyzes your skills and career goals to match you with the best job opportunities.",
    },
    {
      title: "Internship & Training",
      shortDescription: "Secure internships and gain hands-on experience.",
      fullDescription: "We collaborate with leading organizations to offer exclusive internship programs.",
    },
    {
      title: "Mentorship & Guidance",
      shortDescription: "Connect with industry professionals for career advice.",
      fullDescription: "Gain insights from professionals through our mentorship programs.",
    },
  ];

  const toggleFeature = (index) => {
    setExpandedFeature(expandedFeature === index ? null : index);
    setActiveFeature(index);
  };

  return (
  
    
    <div className="mt-32">
      <header className="w-[90%] bg-yellow-600 rounded-2xl text-white text-left pl-8 py-20 flex mx-auto shadow-2xl transition-all duration-300 hover:shadow-[0px_10px_30px_rgba(0,0,0,0.5)] hover:scale-105">
        <div className="w-2/3 p-8">
          <h1 className="text-4xl font-bold">Empowering Your Career Journey</h1>
          <h2 className="mt-2 text-lg font-bold">Find your dream job, internship, or mentor in one place.</h2>
          <p className="mt-3 text-gray-200">An Interactive Job and Internship Platform for Technical Education Department, Govt. of Rajasthan</p>
          <div className="mt-8">
            <Link href="/resumebuilder" className="bg-white text-yellow-600 font-semibold py-2 px-4 rounded mr-4">Get Started</Link>
            <button className="bg-transparent border border-white text-white font-semibold py-2 px-4 rounded">Learn More</button>
          </div>
        </div>
      </header>

      <section className="p-10 text-center">
        <Tabs>
          <TabList className="flex justify-center gap-4">
            <Tab onClick={() => setActiveTab("Why Choose Us?")}>Why Choose Us?</Tab>
            <Tab onClick={() => setActiveTab("How to Apply")}>How to Apply</Tab>
          </TabList>

          <TabPanel>
            <div className="text-left text-white rounded-lg bg-yellow-600 p-4">
              <h1 className="text-bold text-3xl m-3">Advantages over other Platforms</h1>
              <ul>
                <li><strong>- Comprehensive Platform:</strong> Access a wide range of opportunities.</li>
                <li><strong>- Unique Opportunity:</strong> Engage with state-run programs and projects.</li>
                <li><strong>- Hands-on Experience:</strong> Gain real-world exposure.</li>
              </ul>
            </div>
          </TabPanel>

          <TabPanel>
            <div className="text-left rounded-lg bg-yellow-200 p-4">
              <p>Follow these simple steps to apply:</p>
              <ol>
                <li><strong>Step 1:</strong> Visit our platform.</li>
                <li><strong>Step 2:</strong> Register with your details.</li>
                <li><strong>Step 3:</strong> Browse and apply.</li>
              </ol>
            </div>
          </TabPanel>
        </Tabs>

        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mt-6">
          {features.map((feature, index) => (
            <div key={index} className={`p-4 border rounded-lg shadow-lg cursor-pointer ${activeFeature === index ? "bg-yellow-500" : "bg-blue-100"}`} onClick={() => toggleFeature(index)}>
              <h3 className="font-bold text-left">{feature.title}</h3>
              <p className="mt-2 text-left text-gray-800">{expandedFeature === index ? feature.fullDescription : feature.shortDescription}</p>
            </div>
          ))}
        </div>
      </section>
    </div>
  );
};

export default LandingPage;
